.App {
  text-align: center;
}

.App-header {
  background: linear-gradient(#f5b189, #f62f55);
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: white;
}
.title {
  flex-direction: row;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.title-style {
  font-size: 52px;
  margin-block-start: 0px;
  margin-block-end: 0px;
  font-family: AvenirNext-Regular;
}
.coming {
  font-family: Gilroy-Bold;
  font-size: 72px;
  margin-block-start: 0px;
  margin-block-end: 0px;
  text-align: center;
  margin-bottom: 16px;
}
.logo {
  margin-right: 16px;
  align-self: center;
}
.App-link {
  color: #09d3ac;
}
